import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { BsDropdownConfig } from 'ngx-bootstrap/dropdown';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { OrdersService } from './orders.service';

import { LanguageService } from '../../services/language.service';
import { AuthService } from 'src/app/pages/auth/auth.service';
import { Subscription } from 'rxjs';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.scss'],
  providers: [
    {
      provide: BsDropdownConfig,
      useValue: { isAnimated: true, autoClose: true },
    },
  ],
})
export class OrdersComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  private langSub: Subscription;
  currentLang;
  arabic: boolean;

  constructor(
    private authService: AuthService,
    private ordersService: OrdersService,
    private modalService: BsModalService,
    private languageService: LanguageService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private exportAsService: ExportAsService
  ) {
    this.langSub = this.languageService.currentLanguage.subscribe((lang) => {
      this.currentLang = lang;
      if (this.currentLang && this.currentLang === 'ar') {
        this.arabic = true;
      } else {
        this.arabic = false;
      }
    });
  }

  private userSub: Subscription;
  admin = false;
  owner = false;
  shipper = false;
  ngOnDestroy(): void {
    this.userSub.unsubscribe();
    this.langSub.unsubscribe();
  }

  ngOnInit(): void {
    this.userSub = this.authService.user.subscribe((user) => {
      if (user) {
        if (user.admin) {
          this.admin = true;
        }
        if (user.owner) {
          this.owner = true;
        }
        if (user.shipper) {
          this.shipper = true;
        }
      }
    });
    this.spinner.show();

    this.ordersService.getAll();

    this.ordersService.orders.subscribe(async (data) => {
      if (data) {
        this.items = data;

        let x = data;
        x.sort((a, b) =>
          a.confirmed === b.confirmed ? 0 : !a.confirmed ? -1 : 1
        );

        x.sort((a, b) => (a.shipped === b.shipped ? 0 : !a.shipped ? -1 : 1));
        x.sort((a, b) =>
          a.delivered === b.delivered ? 0 : !a.delivered ? -1 : 1
        );

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      // this.tableElements.sort((a, b) =>
      //   !a.confirmed === !b.confirmed ? 0 : !a.confirmed ? -1 : 1
      // );

      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter((item) => {
        for (let i = 0; i < item.products.length; i++) {
          item.products[i].productName.toLowerCase().includes(FilterRecord) ||
            item.seller?.name.toLowerCase().includes(FilterRecord) ||
            item.store?.name.toLowerCase().includes(FilterRecord);
        }
      });
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  itemToConfirm;
  selectItemToConfirm(item) {
    let shipping = 0;
    let total = 0;
    for (let index = 0; index < item.products.length; index++) {
      shipping += item.products[index].shippingCost;
      total += item.products[index].price * item.products[index].qty;
    }
    this.itemToConfirm = item;
    this.itemToConfirm.shipping = shipping;
    this.itemToConfirm.total = total;
  }
  selectItemToPrint(item) {
    let shipping = 0;
    let total = 0;
    for (let index = 0; index < item.products.length; index++) {
      shipping += item.products[index].shippingCost;
      total += item.products[index].price * item.products[index].qty;
    }
    this.itemToConfirm = item;
    this.itemToConfirm.shipping = shipping;
    this.itemToConfirm.total = total;
  }
  printElement() {
    this.exportAs('pdf');
  }

  confirmElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ordersService.confirmOne(this.itemId).subscribe(
      (res) => {
        this.exportAs('pdf');
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ordersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  cancelElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ordersService.cancelOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ordersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  shipElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ordersService.shipOne(this.itemId).subscribe(
      (res) => {
        this.exportAs('pdf');
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ordersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deliverElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.ordersService.deliverOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ordersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'png', // the type you want to download
    elementIdOrContent: 'confirm-receipt', // the id of html/table element
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    // download the file using old school javascript method
    this.exportAsConfig.type = type;
    this.exportAsService.save(this.exportAsConfig, 'Order').subscribe(() => {
      // save started
    });
    // get the data as base64 or json object for json type - this will be helpful in ionic or SSR
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
